@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

@font-face {
    font-family: 'Clarendon-Bold';
    src: url('./Components/Shared//Assets/Fonts/clarendon-bold/Clarendon-Bold.ttf')
        format('truetype');
}

@font-face {
    font-family: 'Maroon';
    src: url('./Components/Shared//Assets/Fonts/maroonedonmars_bb/MaroonedOnMars_BB/MaroonedOnMarsBB.ttf')
        format('truetype');
}

@font-face {
    font-family: 'Uniform';
    src: url('./Components/Shared//Assets/Fonts/Uniform/Uniform Medium.ttf')
        format('truetype');
}

@font-face {
    font-family: 'Legend';
    src: url('./Components/Shared/Assets/Fonts/legend-bold.ttf')
        format('truetype');
}

code {
    font-family: 'Poppins';
}

* {
    margin: 0;
    padding: 0;
}

#root {
    overflow-x: hidden;
}

:root {
    --swiper-theme-color: #5c2945 !important;
}

body {
    margin: 0;
    padding: 0;
    width: 100%;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f6f7f8;
    overflow-x: hidden !important;
    font-family: -apple-system, 'Uniform';
}

.main-wrapper {
    max-width: 1100px;
    width: 100%;
    margin: 0 auto;
}

.load-spinner {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.6);
    z-index: 9999999999999999999999999999999;
}

.load-spinner2 {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(255, 255, 255, 1);
    z-index: 9999999999999999999999999999999;
}

.load-spinner2 h1 {
    font-size: 50px !important;
    width: fit-content !important;
    margin-bottom: 30px;
}

.news-letter-thankyou {
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.news-letter-thankyou h1 {
    color: #5c2945;
}

.news-letter-thankyou a {
    text-decoration: none;
    color: white;
    background-color: rgb(140, 56, 75);
    padding: 10px 20px;
    border: none;
    margin-top: 30px;
}

.annual-order {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 50px 0;
}

.annual-order h1 {
    align-self: flex-start;
    margin: 20px 0;
    color: #8c384b;
}

