.cookie-link {
    /* text-decoration: none; */
    color: #90dae5;
}

.cookie-paragraph {
    width: 90%;
}

.cookie p {
    width: 95%;
    margin-left: 15px;
}

.cookie div {
    margin: 10px auto !important;
}

.music-sound {
    max-width: 1200px;
    margin: 50px auto 30px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

figure#attachment_8074 {
    width: 50%;
    margin: 0 auto;
}

.music-sound h1,
.music-sound h2,
.music-sound h3 {
    color: #5c2945;
}

.music-sound h2 {
    margin: 20px 0;
}

img.size-full.wp-image-8074 {
    width: 100%;
}

.music-sound iframe {
    width: 50%;
}

.footer-popup #inf_field_Email {
    height: 20px !important;
    padding-left: 10px;
}

textarea#inf_custom_OrderNotes,
textarea#inf_custom_GiftMessage,
textarea#inf_custom_Bonus50CardShippingInfo,
textarea#inf_custom_Bonus50GiftNote {
    resize: none;
    padding: 5px;
    border: 1px solid #90dae5;
    background-color: #f6f7f8;
    border-radius: 10px;
    outline: none;
}

input#inf_field_Email {
    border: 1px solid #90dae5;
    background-color: #f6f7f8;
    border-radius: 10px;
}

input#inf_custom_Child1,
input#inf_custom_Child2,
input#inf_custom_Child3,
input#inf_custom_Child4,
input#inf_custom_Child5,
input#inf_custom_Child6,
input#inf_custom_Child7,
input#inf_custom_Age8Birthdate,
input#inf_custom_Age9Birthdate,
input#inf_custom_Age10Birthdate,
input#inf_custom_Age11Birthdate,
input#inf_custom_Age12aboveBirthdate,
input#inf_custom_ShippingEmail,
input#inf_custom_Bonus50CardRecipient,
input#inf_custom_Bonus50CardRecipientEmail {
    border: 1px solid #90dae5;
    background-color: #f6f7f8;
    border-radius: 10px;
    height: 30px;
    padding: 5px;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

@media screen and (max-width: 998px) {
    .music-sound iframe {
        width: 90%;
    }

    .cookie {
        flex-direction: column;
        z-index: 999999 !important;
        padding: 5px 0 !important;
    }

    .cookie button {
        margin: 10px 0 !important;
    }

    .cookie div {
        margin: 0 !important;
    }

    .annual-order img {
        width: 100%;
    }

    .cookie {
        flex-direction: column;
    }

    .cookie div {
        flex: unset !important;
        text-align: center;
        align-items: center;
        justify-content: center;
        display: flex;
        flex-direction: row;
        width: 100%;
    }

    .cookie p {
        width: 95%;
        margin-left: 0;
    }
}
