    width: 90%;
  }

  .about-firstSection-text p {
    width: 90%;
    text-align: center;
    margin-top: 20px;
    font-size: 18px;
  }

  .about-secondSection img {
    max-width: 350px;
    width: 90%;
  }

  .about-thirdSection p {
    width: 90%;
    font-size: 18px;
  }

  .about-cards-desktop {
    display: none;
  }

  .about-cards-mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 500px;
  }

  .about-card {
    background-repeat: no-repeat !important;
    background-size: cover !important;
    width: 100%;
    /* margin: 0 auto; */
    /* border-radius: 10px; */
    /* box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.41);
    -webkit-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.41);
    -moz-box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.41);
    margin-top: 10px; */
  }

  .about-fourthSection {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .about-fourthSection h1 {
    width: 90%;
    font-size: 31px;
  }

  .about-fourthSection h1>span {
    font-size: 15px;
  }

  .about-fourthSection p {
    width: 90%;
    margin-top: 20px;
