/* **************FIRST SECTION*************** */
.first-section-pk {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0px 0;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.pk-first-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.first-section-pk-text {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.first-section-pk-text h1 {
  font-size: 40px;
  color: #ffffff;
}

.first-section-pk-text h2 {
  color: #ffffff;
  font-size: 31px;
  font-weight: 400;
  padding-top: 10px;
}

.first-section-pk-text p {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  padding: 30px 0;
}

.first-section-pk-text a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  font-size: 20px;
  background-color: #90dae5;
  padding: 15px 35px;
  border-radius: 4px;
}

/* *****************SECOND SECTION***************** */
.second-section-pk {
  width: 100%;
  height: fit-content;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}

.pk-second-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.second-section-pk p {
  width: 280px;
  text-align: center;
  color: #601b3a;
  font-size: 20px;
  padding-bottom: 0px;
}

.second-section-pk p span {
  font-weight: bold;
  font-size: 40px;
  padding-top: 15px;
}

.promises {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* padding: 20px 0; */
}

.promise {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 300px;
  padding: 20px 0;
  margin: 0 10px 0 0;
}

.promise p {
  text-align: left;
  padding-left: 10px;
  width: 200px;
  font-size: 18px;
  color: #601b3a;
  font-weight: bold;
}

.promise-img {
  width: 90px;
  height: 90px;
  background-color: #c4c4c4;
  border-radius: 100%;
  display: block;
}

/* **********Third section*************** */
.third-section-pk {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 30px 0;
  background-color: white;
}

.pk-third-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.third-section-pk-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-right: 70px;
}

.third-section-pk-text h1 {
  font-size: 40px;
  color: #f2b21c;
}

.third-section-pk-text p {
  color: black;
  font-size: 18px;
  font-weight: 400;
  width: 500px;
  padding-top: 20px;
}

/* **************FOURTH SECTION************ */
.fourth-section-pk {
  width: 100%;
  /* padding: 40px 0; */
  /* background-color: #bb3f6e; */
  background-repeat: no-repeat;
  background-size: 50% 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.fourth-section-pk-text {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  /* padding-left: 100px; */
}

.pk-fourth-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 320px;
}

.pk-fourth-section img {
  width: 40%;
  height: 100%;
}

.fourth-section-pk-text {
  width: 50%;
}

.fourth-section-pk-text h1 {
  font-family: 'Maroon';
  color: #f3b21c;
  font-weight: 400;
  font-size: 30px;
}

.fourth-section-pk-text h2 {
  color: white;
  font-size: 30px;
  font-family: 'Clarendon-Bold';
  padding-bottom: 20px;
}

.fourth-section-pk-text h3 {
  font-family: 'Maroon';
  color: #ffffff;
  font-weight: 400;
  font-size: 31px;
}

.fourth-section-pk-text h4 {
  color: white;
  font-size: 41px;
  font-family: 'Clarendon-Bold';
}

.fourth-section-pk-text span {
  color: #f3b21c;
  font-family: 'Clarendon-Bold';
  font-weight: bold;
}

#sneak {
  padding-bottom: 20px;
}

/* *********fith secton********* */
.fifth-section-pk {
  width: 100%;
  margin: 0 auto;
  padding: 40px 0;
  display: flex;
  font-weight: 400;
  flex-direction: column;
}

.fifth-section-pk h1 {
  font-family: 'Maroon';
  font-size: 30px;
  color: #5c2945;
  font-weight: 400;
}

.fifth-section-pk h2 {
  font-size: 30px;
  color: #c64f6f;
  font-family: 'Clarendon-Bold';
  font-weight: bold;
}

.fifth-section-pk h3 {
  color: #c74e70;
  font-size: 18px;
  font-weight: 400;
  padding-top: 20px;
  font-family: 'Clarendon-Bold';
}

.promises-tabs {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 20px;
  align-items: center;
}

.tab {
  width: 160px;
  height: 60px;
  border-radius: 10px;
  border: 1px solid #f2b21c;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

#tab1 {
  background-color: #ec6f2f21;
}

#tab2 {
  background-color: #7fc2b921;
}

#tab3 {
  background-color: #b2a93321;
}

#tab4 {
  background-color: #f3b21c21;
}

.tab img {
  height: 80%;
}

.dots {
  width: 130px;
  height: 0px;
  border: 1px dotted #7b3d5d;
  opacity: 0.5;
}

.tabsOpacity {
  opacity: 0.3;
  transition: all 0.2s ease-in-out;
}

.tab1 {
  width: 95%;
  margin: 30px auto;
  /* height: 400px; */
  background-color: #ec6f2f0d;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 40px 2.5%;
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.17);
  -webkit-box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.17);
  -moz-box-shadow: 0px 5px 5px 1px rgba(0, 0, 0, 0.17);
}

.tab1-text,
.tab2-text {
  width: 405px;
}

.tab1-text h2 {
  color: #eb6f30;
  font-size: 18px;
  font-family: 'Clarendon-Bold';
  padding: 0;
  font-weight: bold;
}

.tab1 img {
  width: 50%;
  height: 350px;
  border-radius: 40px;
}

.tab1-text h1 {
  color: #7b3d5d;
  font-family: 'Clarendon-Bold';
  font-size: 25px;
  padding: 0;
  font-weight: bold;
}

.tab1-text p,
.tab2-text p {
  font-family: 'Uniform';
  color: black;
  font-weight: 400;
  padding-top: 20px;
  font-size: 14px;
}

.tab2-text h1 {
  color: #7fc2b9;
  font-family: 'Clarendon-Bold';
  font-size: 30px;
  font-weight: bold;
  padding: 0;
}

.tab2-text h2 {
  color: #c64f6f;
  padding: 0;
  padding: 0;
  font-weight: bold;
  font-size: 20px;
}

#tab3-h1 {
  color: #f3b21c;
  font-size: 20px;
}

.tab4-h1 {
  color: #b2a933 !important;
  font-size: 20px !important;
}

.desktop-animation {
  display: block;
}

.mobile-animation {
  display: none;
}

#tab3-h2 {
  color: #b2a933;
  font-size: 35px;
}

#tab4-h2 {
  color: #f3b21c;
  font-size: 30px;
}

#tab5-h1 {
  color: #7b3d5d;
  font-size: 20px;
}

#tab5-h2 {
  color: #7eb2ad;
  font-size: 30px;
}

/* **********SIXTH SECTION********** */

.sixth-section-pk {
  width: 100%;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pk-sixth-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.sixth-section-pk h1 {
  font-family: 'Maroon';
  font-size: 30px;
  font-weight: 400;
  color: #5c2945;
}

.sixth-section-pk h2 {
  color: #c64f6f;
  font-family: 'Clarendon-Bold';
  font-size: 35px;
  /* width: 30px; */
}

.sixth-section-pk img {
  margin: 30px 0;
  width: 100%;
}

.sixth-section-pk p {
  width: 540px;
  font-size: 20px;
  font-weight: 400;
}

/* ********SEVENTH SECTION******* */
.seventh-section-pk {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 40px 0;
  margin: 10px 0 0px;
}

.pk-seventh-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.pk-seventh-section-inverse {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}

.seventh-section-pk h1 {
  font-size: 30px;
  font-family: 'Maroon';
  font-weight: 400;
  color: #5c2945;
}

.seventh-section-pk h2 {
  font-size: 35px;
  color: #c64f6f;
  font-family: 'Clarendon-Bold';
  /* width: 300px; */
}

.seventh-section-pk p {
  color: black;
  font-size: 20px;
  padding-top: 20px;
  width: 450px;
}

.seventh-inverse {
  flex-direction: row-reverse;
}

.seventh-inverse p {
  width: 600px;
}

/* ********Eighth Section********* */
.eighth-section-pk {
  width: 100%;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pk-eighth-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.eighth-section-pk h1 {
  font-size: 30px;
  font-family: 'Maroon';
  font-weight: 400;
  color: #5c2945;
}

.eighth-section-pk h2 {
  font-family: 'Clarendon-Bold';
  font-size: 35px;
  color: #c64f6f;
  font-weight: 400;
}

.eighth-section-pk p {
  width: 100%;
  /* text-align: center; */
