  font-size: 20px;
  font-weight: 400;
}

.eighth-section-images {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
}

.eighth-section-image {
  width: 390px;
  height: 390px;
  position: relative;
