}

.eighth-image-text2 {
  width: 150px !important;
  height: 150px !important;
  position: absolute;
  right: -110px;
  top: 0px;
}

/* **************NINTH SECTION************* */
.ninth-section-pk {
  /* width: 100%; */
  height: fit-content;
  padding: 40px 35px;
  margin: 100px auto;
  background-color: #5c2945;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.pk-ninth-section {
