}

.ninth-section-pk-text a {
  text-decoration: none;
  color: #ffffff;
  padding: 15px 35px;
  background-color: #f2b21c;
  border-radius: 4px;
  font-size: 20px;
}

.fifth-section-pk-desktop {
  display: block;
}

.fifth-section-pk-mobile {
  display: none;
}

.desktop-image-tab {
  display: block;
}

.mobile-image-tab {
  display: none;
}

.sixth-section-pk-text {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.sixth-section-pk-text img {
  width: 50%;
}

.sixth-section-pk-text p {
  text-align: left;
}

.reusable-section-image {
  width: 50%;
  padding-right: 20px;
}

.pk-fifth-section-h2 {
  width: 60%;
}

