.slick-next,
.before-next {
  right: 40%;
  top: 300px;
  position: absolute;
  cursor: pointer;
}

/* .slick-next::before,
.slick-prev::before {
  content: ">";
} */

.before-next ::before {
  position: absolute;
  right: 32%;
  top: 300px;
  cursor: pointer;
}

.before-prev::before {
  position: absolute;
  left: 32%;
  top: 300px;
  cursor: pointer;
}

.before-prev {
  left: 40%;
  top: 300px;
  margin: 0;
  position: absolute;
  cursor: pointer;
}

