}

.slick-dots {
  bottom: -50px;
  z-index: -1;
}

.slick-dots li button::before {
  color: transparent;
  width: 10px;
  height: 10px;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 10px;
  height: 10px;
