.footer-main {
  width: 100%;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: auto;
}

.footer1 {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 90%;
  max-width: 1200px;
}

.logo-subscribe {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 350px;
  padding-top: 60px;
  position: relative;
}

.logo-subscribe img {
  width: 250px;
}

.logo-subscribe p {
  color: #1e2833;
  font-weight: 400;
  size: 18px;
  padding-top: 20px;
}

.logo-subscribe h6 {
  font-size: 20px;
  color: #5c2945;
  padding-top: 35px;
}

.email-footer {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid rgba(92, 107, 120, 0.5);
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  width: 80%;
  height: 40px;
  padding-left: 3px;
  font-size: 15px;
  outline: none;
  padding-top: 8px;
}

.thankyou-message {

  color: #1e2833;
  font-weight: 400;
  size: 18px;
  padding-top: 20px;
  bottom: -30px;

}

.email-footer::placeholder {
  color: rgba(92, 107, 120, 0.5);
}

.submit-footer {
  height: 50px;
  width: 50px;
  padding-bottom: 2px;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  outline: none;
  background-color: #5c2945;
  color: white;
  border: 1px solid #5c2945;
  cursor: pointer;
}

.newsletter-footer {
  padding-top: 30px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.footer-links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding-top: 60px;
  padding-left: 100px;
}

.footer-links h1 {
  color: #5c2945;
  font-size: 20px;
  font-weight: bold;
  padding-bottom: 10px;
}

.footer-links a {
  color: #1e2833;
  font-size: 16px;
  text-decoration: none;
  padding: 10px 0;
  font-weight: 400;
  text-align: left;
}

.social {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 150px;
  text-align: left;
}

.footer-popup-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 11111111;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-popup {
  width: 700px;
  height: 300px;
  background-color: white;
  outline: 15px solid rgba(0, 0, 0, 0.5);
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 40px 20px;
  position: relative;

}

.popup-form {
  width: 700px;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.popup-heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup-heading p {
  font-size: 15px;
  color: #5d5d5d;
}

.popup-inputs {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup-form form#inf_form_8798ebd237af8b6c070d3ff7f0bb50c8 {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.popup-form img.simage {
  height: 60px;
}

.popup-form .infusion-field {
  width: 100%;
  margin: 15px 0;
}

.popup-form .infusion-field span {
  height: 20px;
}

.popup-form input#inf_field_FirstName {
  width: 250px;
  background-color: #e9edf0;
  border: none;
  height: 25px;
  text-align: center;
  color: #9da9b3;
  /* outline: none; */
}


.name-footer {
  width: 100%;
  height: 35px;
  border: none;
  background-color: #e9edf0;
  text-align: center;
  outline: none;
  color: #9da9b3;
}

.footer-radio-btns {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 15px 0;
}

.footer-radio-btns p {
  color: #5d5d5d;
  padding: 0;
  width: fit-content;
}

.radio-btns-options {
  margin: 0 7px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.popup-terms {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.popup-terms label {
  margin: 0 10px;
  padding: 0;
  color: #5d5d5d;
  font-size: 15px;
}

.popup-terms a {
  color: #1193bb;
}

.radio-btns-options label {
  padding: 0 5px;
  font-size: 13px;
  color: #5d5d5d;
}

.footer-popup button {
  padding: 5px 15px;
  background-color: #8c384b;
  border: none;
  color: white;
  font-family: "Legend";
  font-size: 20px;
  font-weight: bold;
  outline: none;
}

.footer-popup-close-icon {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.social div {
  width: 20px;
}

.social a {
  padding-left: 20px;
  cursor: pointer;
}

.footer2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  height: auto;
  padding: 60px 0 30px 0;
}

.footer2 p {
  color: #1e2833;
  font-size: 14px;
  font-weight: 400;
  opacity: 0.5;
  padding-right: 170px;
}

.terms-privacy a {
  color: #1e2833;
  text-decoration: none;
  padding: 0 30px;
}

.main-footer-thankyou {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.main-footer-thankyou p {
  color: #5d5d5d;
  font-size: 14px;
}

.main-footer-thankyou h2 {
  margin-top: 30px;
  color: #5c2945;
}

.infusion-option label {
  margin: 0 5px;
}

.infusion-checkbox label {
  margin: 0 5px;
}

.first-name-footer {
  display: flex;
  flex-direction: column;
}

.first-name-footer input {
  margin-top: 5px;
}

.infusion-option-footer {
  margin: 0 5px;
}

@media screen and (max-width: 998px) {
  .footer1 {
    flex-direction: column;
  }

  .footer-links {
    text-align: center;
    padding-left: 0px;
    width: 100%;
    align-items: center;
    padding-top: 20px;
  }

  .footer2 {
    flex-direction: column;
    padding: 0;
  }

  .footer2 p {
    padding-right: 00px;
  }

  .terms-privacy {
    padding: 0;
    text-align: center;
    padding: 33px 0;
  }
}

@media screen and (max-width:790px) {
  .footer-popup {
    width: 85%;
    height: 350px;
    justify-content: space-between;
  }

  .name-footer {
    width: 100%;
  }

  .popup-heading {
    width: 90%;
  }

  .popup-form {
    width: 100%;
    height: 100%;
    justify-content: space-between;
  }

  .footer-radio-btns {
    flex-direction: column;
    justify-content: space-between;
    height: 120px;
  }

  .footer-radio-btns p>span {
    display: none;
  }

}